/**
 * HomeScreen Ïú†Ìã∏Î¶¨Ìã∞ Ìï®ÏàòÎì§Ïóê ÎåÄÌïú Ïú†Îãõ ÌÖåÏä§Ìä∏
 * 
 * Ïù¥ ÌÖåÏä§Ìä∏Îäî HomeScreen Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Ï∂îÏ∂úÌïú Ïú†Ìã∏Î¶¨Ìã∞ Ìï®ÏàòÎì§ÎßåÏùÑ ÌÖåÏä§Ìä∏Ìï©ÎãàÎã§.
 * Ïù¥Î†áÍ≤å ÌïòÎ©¥ React NativeÏùò UI Î†åÎçîÎßÅÍ≥º Í¥ÄÎ†®Îêú Î≥µÏû°ÏÑ±ÏùÑ ÌîºÌïòÎ©¥ÏÑú
 * ÌïµÏã¨ ÎπÑÏ¶àÎãàÏä§ Î°úÏßÅÏùÑ ÌÖåÏä§Ìä∏Ìï† Ïàò ÏûàÏäµÎãàÎã§.
 */

// ÌÖåÏä§Ìä∏Ìï† Ìï®ÏàòÎì§ ÏßÅÏ†ë Ï†ïÏùò (HomeScreenÏóêÏÑú ÏûÑÌè¨Ìä∏ÌïòÏßÄ ÏïäÍ≥†!)
// Ïù¥Î†áÍ≤å ÌïòÎ©¥ ÏùòÏ°¥ÏÑ± Î¨∏Ï†úÎ•º ÏôÑÏ†ÑÌûà ÌöåÌîºÌï† Ïàò ÏûàÏäµÎãàÎã§.

// ÌÉÄÏûÖ Ï†ïÏùò
type Emotion = {
    label: string;
    icon: string;
    color: string;
  };
  
  type PostComment = {
    id: number;
    author: string;
    content: string;
  };
  
  type Post = {
    id: number;
    anonymousId: string;
    content: string;
    emotion: string;
    emotionIcon: string;
    image: string;
    likes: number;
    comments: PostComment[]; // Ïù¥Î¶Ñ Î≥ÄÍ≤ΩÎêú ÌÉÄÏûÖ ÏÇ¨Ïö©
    timestamp: string;
  };
  
  // ÌÖåÏä§Ìä∏Ïö© Í∞êÏ†ï Îç∞Ïù¥ÌÑ∞
  const testEmotions: Emotion[] = [
    { label: 'ÌñâÎ≥µ', icon: 'emoticon-happy-outline', color: '#FFD700' },
    { label: 'Í∞êÏÇ¨', icon: 'hand-heart', color: '#FF69B4' },
  ];
  
  // ÌÖåÏä§Ìä∏Ïö© Í≤åÏãúÎ¨º Îç∞Ïù¥ÌÑ∞
  const testPosts: Post[] = [
    {
      id: 1,
      anonymousId: 'ÏùµÎ™Ö1',
      content: 'ÌÖåÏä§Ìä∏ Í≤åÏãúÎ¨º',
      emotion: 'ÌñâÎ≥µ',
      emotionIcon: 'üòä',
      image: '',
      likes: 5,
      comments: [],
      timestamp: '1ÏãúÍ∞Ñ Ï†Ñ'
    }
  ];
  
  // handlePostSubmission Ìï®Ïàò Ï†ïÏùò
  function handlePostSubmission(
    content: string, 
    emotion: Emotion | null, 
    imageUrl: string, 
    setIsLoading: (value: boolean) => void,
    setIsDialogVisible: (value: boolean) => void
  ): boolean {
    if (content && emotion) {
      setIsLoading(true);
      // Í≤åÏãúÎ¨º ÏóÖÎ°úÎìú Î°úÏßÅÏùÑ ÏãúÎÆ¨Î†àÏù¥Ìä∏
      setTimeout(() => {
        setIsLoading(false);
        setIsDialogVisible(true);
      }, 1000);
      return true;
    }
    return false;
  }
  
  // handleImageUploadAction Ìï®Ïàò Ï†ïÏùò
  function handleImageUploadAction(
    setImageUrl: (url: string) => void
  ): void {
    setImageUrl('https://via.placeholder.com/150');
  }
  
  // handleLikeAction Ìï®Ïàò Ï†ïÏùò
  function handleLikeAction(
    posts: Post[],
    postId: number, 
    setPosts: (posts: Post[]) => void
  ): void {
    setPosts(posts.map(post =>
      post.id === postId ? { ...post, likes: post.likes + 1 } : post
    ));
  }
  
// Ìï®Ïàò Ï†ïÏùò ÏàòÏ†ï
function handleCommentAction(
    posts: Post[],
    postId: number, 
    commentContent: string,
    setPosts: (posts: Post[]) => void
  ): void {
    // ÏÉà ÎåìÍ∏Ä Í∞ùÏ≤¥ ÏÉùÏÑ±
    const newComment: PostComment = {
      id: Date.now(),
      author: 'ÏùµÎ™Ö',
      content: commentContent
    };
    
    // Í≤åÏãúÎ¨º ÏóÖÎç∞Ïù¥Ìä∏
    setPosts(posts.map(post =>
      post.id === postId
        ? {
            ...post,
            comments: [
              ...post.comments,
              newComment
            ]
          }
        : post
    ));
  }
  
  // Ïã§Ï†ú ÌÖåÏä§Ìä∏ ÏΩîÎìú
  describe('HomeScreen Ïú†Ìã∏Î¶¨Ìã∞ Ìï®Ïàò', () => {
    // ÌÖåÏä§Ìä∏ Ï†ÑÏóê Ïã§Ìñâ
    beforeEach(() => {
      jest.clearAllMocks();
      jest.useFakeTimers();
    });
  
    // ÌÖåÏä§Ìä∏ ÌõÑÏóê Ïã§Ìñâ
    afterEach(() => {
      jest.useRealTimers();
    });
  
    // handlePostSubmission ÌÖåÏä§Ìä∏
    describe('handlePostSubmission Ìï®Ïàò', () => {
      it('ÎÇ¥Ïö©Í≥º Í∞êÏ†ïÏù¥ ÏûàÏùÑ Îïå trueÎ•º Î∞òÌôòÌï¥Ïïº Ìï®', () => {
        // Mock Ìï®Ïàò ÏÉùÏÑ±
        const setIsLoading = jest.fn();
        const setIsDialogVisible = jest.fn();
        
        // ÌÖåÏä§Ìä∏ Îç∞Ïù¥ÌÑ∞
        const content = 'ÌÖåÏä§Ìä∏ Í≤åÏãúÎ¨º';
        const emotion = testEmotions[0]; // 'ÌñâÎ≥µ' Í∞êÏ†ï
        const imageUrl = 'https://example.com/image.jpg';
        
        // Ìï®Ïàò Ìò∏Ï∂ú
        const result = handlePostSubmission(
          content, 
          emotion, 
          imageUrl, 
          setIsLoading, 
          setIsDialogVisible
        );
        
        // Í≤ÄÏ¶ù
        expect(result).toBe(true);
        expect(setIsLoading).toHaveBeenCalledWith(true);
        
        // setTimeoutÏùÑ ÏãúÎÆ¨Î†àÏù¥ÏÖò
        jest.advanceTimersByTime(1000);
        
        // ÌÉÄÏù¥Î®∏ ÏôÑÎ£å ÌõÑ ÏÉÅÌÉú Í≤ÄÏ¶ù
        expect(setIsLoading).toHaveBeenCalledWith(false);
        expect(setIsDialogVisible).toHaveBeenCalledWith(true);
      });
      
      it('ÎÇ¥Ïö©Ïù¥ ÏóÜÏùÑ Îïå falseÎ•º Î∞òÌôòÌï¥Ïïº Ìï®', () => {
        // Mock Ìï®Ïàò ÏÉùÏÑ±
        const setIsLoading = jest.fn();
        const setIsDialogVisible = jest.fn();
        
        // ÎÇ¥Ïö© ÏóÜÏùå
        const content = '';
        const emotion = testEmotions[0]; // 'ÌñâÎ≥µ' Í∞êÏ†ï
        const imageUrl = 'https://example.com/image.jpg';
        
        // Ìï®Ïàò Ìò∏Ï∂ú
        const result = handlePostSubmission(
          content, 
          emotion, 
          imageUrl, 
          setIsLoading, 
          setIsDialogVisible
        );
        
        // Í≤ÄÏ¶ù
        expect(result).toBe(false);
        expect(setIsLoading).not.toHaveBeenCalled();
        expect(setIsDialogVisible).not.toHaveBeenCalled();
      });
      
      it('Í∞êÏ†ïÏù¥ ÏóÜÏùÑ Îïå falseÎ•º Î∞òÌôòÌï¥Ïïº Ìï®', () => {
        // Mock Ìï®Ïàò ÏÉùÏÑ±
        const setIsLoading = jest.fn();
        const setIsDialogVisible = jest.fn();
        
        // Í∞êÏ†ï ÏóÜÏùå
        const content = 'ÌÖåÏä§Ìä∏ Í≤åÏãúÎ¨º';
        const emotion = null;
        const imageUrl = 'https://example.com/image.jpg';
        
        // Ìï®Ïàò Ìò∏Ï∂ú
        const result = handlePostSubmission(
          content, 
          emotion, 
          imageUrl, 
          setIsLoading, 
          setIsDialogVisible
        );
        
        // Í≤ÄÏ¶ù
        expect(result).toBe(false);
        expect(setIsLoading).not.toHaveBeenCalled();
        expect(setIsDialogVisible).not.toHaveBeenCalled();
      });
    });
    
    // handleImageUploadAction ÌÖåÏä§Ìä∏
    describe('handleImageUploadAction Ìï®Ïàò', () => {
      it('Ïù¥ÎØ∏ÏßÄ URLÏùÑ ÏÑ§Ï†ïÌï¥Ïïº Ìï®', () => {
        // Mock Ìï®Ïàò ÏÉùÏÑ±
        const setImageUrl = jest.fn();
        
        // Ìï®Ïàò Ìò∏Ï∂ú
        handleImageUploadAction(setImageUrl);
        
        // Í≤ÄÏ¶ù
        expect(setImageUrl).toHaveBeenCalledWith('https://via.placeholder.com/150');
      });
    });
    
    // handleLikeAction ÌÖåÏä§Ìä∏
    describe('handleLikeAction Ìï®Ïàò', () => {
      it('Ï¢ãÏïÑÏöî ÏàòÎ•º Ï¶ùÍ∞ÄÏãúÏºúÏïº Ìï®', () => {
        // ÌÖåÏä§Ìä∏ Îç∞Ïù¥ÌÑ∞
        const posts = [...testPosts]; // Í∏∞Ï°¥ Í≤åÏãúÎ¨º Î≥µÏÇ¨
        const postId = 1;
        const setPosts = jest.fn();
        
        // Ìï®Ïàò Ìò∏Ï∂ú
        handleLikeAction(posts, postId, setPosts);
        
        // Í≤ÄÏ¶ù
        expect(setPosts).toHaveBeenCalled();
        
        // setPostsÏóê Ï†ÑÎã¨Îêú ÏÉà Í≤åÏãúÎ¨º Î∞∞Ïó¥ Í∞ÄÏ†∏Ïò§Í∏∞
        const updatedPosts = setPosts.mock.calls[0][0];
        
        // Ï¢ãÏïÑÏöî ÏàòÍ∞Ä Ï¶ùÍ∞ÄÌñàÎäîÏßÄ ÌôïÏù∏
        expect(updatedPosts[0].likes).toBe(6);
      });
    });
    
    // handleCommentAction ÌÖåÏä§Ìä∏
    describe('handleCommentAction Ìï®Ïàò', () => {
      it('ÎåìÍ∏ÄÏùÑ Ï∂îÍ∞ÄÌï¥Ïïº Ìï®', () => {
        // ÌÖåÏä§Ìä∏ Îç∞Ïù¥ÌÑ∞
        const posts = [...testPosts]; // Í∏∞Ï°¥ Í≤åÏãúÎ¨º Î≥µÏÇ¨
        const postId = 1;
        const commentContent = 'ÌÖåÏä§Ìä∏ ÎåìÍ∏Ä';
        const setPosts = jest.fn();
        
        // Date.now Î™®ÌÇπ
        const mockDateNow = 123456789;
        jest.spyOn(Date, 'now').mockImplementation(() => mockDateNow);
        
        // Ìï®Ïàò Ìò∏Ï∂ú
        handleCommentAction(posts, postId, commentContent, setPosts);
        
        // Í≤ÄÏ¶ù
        expect(setPosts).toHaveBeenCalled();
        
        // setPostsÏóê Ï†ÑÎã¨Îêú ÏÉà Í≤åÏãúÎ¨º Î∞∞Ïó¥ Í∞ÄÏ†∏Ïò§Í∏∞
        const updatedPosts = setPosts.mock.calls[0][0];
        
        // ÎåìÍ∏ÄÏù¥ Ï∂îÍ∞ÄÎêòÏóàÎäîÏßÄ ÌôïÏù∏
        expect(updatedPosts[0].comments.length).toBe(1);
        expect(updatedPosts[0].comments[0].content).toBe('ÌÖåÏä§Ìä∏ ÎåìÍ∏Ä');
        expect(updatedPosts[0].comments[0].id).toBe(mockDateNow);
        expect(updatedPosts[0].comments[0].author).toBe('ÏùµÎ™Ö');
      });
    });
  });